import Head from "next/head";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import styles from "./../ui/styles/General.module.css";

const InitialPage: React.FC = () => {
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();

  useEffect(() => {
    const timer = setTimeout(() => setIsLoading(false), 300);
    return () => clearTimeout(timer);
  }, []);

  const pushLoginPage = () => {
    router.push("/loginPage");
  };

  const pushRegisterPage = () => {
    router.push("/RegisterPage");
  };

  return (
    <div className={styles.pageContainer}>
      <Head>
        <title>Bem-vindo - VL Store</title>
        <meta name="viewport" content="width=device-width, initial-scale=1.0" />
      </Head>

      <header className={styles.header}>
        <div className={styles.headerContent}>
          <img
            src="/vl-store-logo-white.svg"
            alt="VL Store Logo"
            className={styles.logo}
          />
        </div>
      </header>

      <main className={styles.mainContent}>
        <div className={styles.cardContainer}>
          {/* Painel de Informações */}
          <div className={styles.infoPanel}>
            <h4>VL Store</h4>
            <p>
              Seja bem-vindo à VL Store, a plataforma de gerenciamento
              empresarial da VL Company.
            </p>
          </div>

          {/* Painel de Ações */}
          <div className={styles.actionPanel}>
            <img
              src="/vl-store-logo-white.svg"
              alt="VL Store Logo"
              className={styles.actionLogo}
            />
            <button onClick={pushLoginPage} className={styles.primaryButton}>
              Entrar com email e senha
            </button>
            <button onClick={pushRegisterPage} className={styles.primaryButton}>
              Cadastrar
            </button>
          </div>
        </div>
      </main>

      <footer className={styles.footer}>
        <p>© 2025 VL Company - Todos os direitos reservados</p>
      </footer>
    </div>
  );
};

export default InitialPage;
